@font-face {
    font-family: EconomicaBold;
    src: url(assets/fonts/Economica-Bold.ttf);
}

@font-face {
    font-family: MontserratMedium;
    src: url(assets/fonts/Montserrat-Medium.ttf);
    font-weight: 800;
}

body {
    text-align: center;
    font-size: 20px;
    padding: 2.5%;
    font-family: 'MontserratMedium';
}

h1, h2, h3, h4, h5 {
    font-family: 'EconomicaBold';
}

p1 {
    font-size: 15px;

}

p2 {
    display: block;
    margin-top: .5em;
    margin-bottom: .5em;
    margin-left: 0;
    margin-right: 0;
    font-size: 16px;
}

.profile {
    width: 300px;
    height: 300px;
    border-radius: 150px;
}